import React, { useEffect, useState } from 'react';
import { Button, Col, Container, Row, Table } from 'reactstrap';
import { Rep } from '../Repetition/Rep';

export const Serie = ({ numSeries, numReps, editable, size, weigth }) => {
  const [series, setSeries] = useState(Array.from(Array(numSeries).keys()));
  const [isEditable, setIsEditable] = useState(editable);
  const [_weigth, setWeigth] = useState(weigth);
  const [_numReps, setNumReps] = useState(numReps);
  const [title, setTitle] = useState(size);

  useEffect(() => {
    setIsEditable(editable);
    console.log(numSeries);
    setSeries(Array.from(Array(numSeries).keys()));
    console.log(series);
    setWeigth(weigth);
    setNumReps(numReps);
  }, [editable, numSeries, numReps, weigth, size]);

  const handleOnSave = () => {
    alert('Saved!!');
  };

  const handleOnAdd = () => {
    setSeries([...series, series.length + 1]);
  };

  const handleOnDelete = () => {
    setSeries(series.filter(id => id !== series.length));
  };

  return (
    <Container>
      <Row>
        <h2>This is a {title} size serie</h2>
      </Row>
      <Row>
        <Table>
          <thead>
            <tr>
              <th>#</th>
              <th>#repetitions</th>
              <th>weight</th>
            </tr>
          </thead>
          <tbody>
            {series.map(r => (
              <Rep key={r} id={r} numReps={_numReps} weigth={_weigth} editable={isEditable}></Rep>
            ))}
          </tbody>
        </Table>
      </Row>
      <Col xs>
        <Button color="success" onClick={handleOnSave}>
          Save
        </Button>
      </Col>
      <Col>
        <Button color="primary" onClick={handleOnAdd}>
          +
        </Button>
        <Button color="danger" onClick={handleOnDelete}>
          -
        </Button>
      </Col>
    </Container>
  );
};
